@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Bold.woff2") format("woff2"), url("../fonts/Rubik-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../fonts/Rubik-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Medium.woff2") format("woff2"), url("../fonts/Rubik-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

body {
  margin: 0;
  font-family: "Rubik", sans-serif;
  font-weight: normal;
}
@media (max-width: 425px) {
  body {
    overflow-x: hidden;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1568px;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  margin-top: 40px;
  justify-content: center;
}
@media (min-width: 2500px) {
  .container {
    align-items: center;
  }
}
@media (max-width: 425px) {
  .container {
    max-width: 100%;
    overflow: hidden;
  }
}

.button {
  cursor: pointer;
}

.catalog-breadcrums {
  margin: 0 auto;
  margin-bottom: 40px;
  max-width: 1396px;
}
@media (max-width: 1440px) {
  .catalog-breadcrums {
    max-width: 1042px;
  }
}
@media (max-width: 1024px) {
  .catalog-breadcrums {
    max-width: 688px;
  }
}
@media (max-width: 768px) {
  .catalog-breadcrums {
    max-width: 538px;
  }
}
@media (max-width: 425px) {
  .catalog-breadcrums {
    padding-left: 20px;
  }
}

.breadcrums-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 25px;
  align-items: center;
}
@media (max-width: 768px) {
  .breadcrums-list {
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    margin-bottom: 27px;
  }
}

.breadcrums-item > a:not(.active) {
  border-bottom: 2px dotted #535658;
}
@media (max-width: 768px) {
  .breadcrums-item {
    margin-bottom: 5px;
  }
  .breadcrums-item:not(:first-child) {
    margin-bottom: 7px;
  }
  .breadcrums-item:last-child {
    margin-bottom: 0;
  }
}

.breadcrums-link {
  text-decoration: none;
  font-size: 24px;
  line-height: 34px;
  color: #535658;
  transition: all 0.3s ease-in;
}
.breadcrums-link:hover {
  color: #212324;
}
.breadcrums-link.active {
  text-transform: uppercase;
  font-weight: bold;
  color: #000000;
  font-size: 34px;
  line-height: 40px;
  transition: all 0.3s ease-in;
}
.breadcrums-link.active:hover {
  color: #5f6365;
}

@media (max-width: 1024px) {
  .breadcrums-link {
    font-size: 18px;
    line-height: 25px;
  }
  .breadcrums-link.active {
    font-size: 21px;
    line-height: 25px;
  }
}
@media (max-width: 768px) {
  .breadcrums-link {
    font-size: 14px;
    line-height: 22px;
  }
  .breadcrums-link.active {
    font-size: 18px;
    line-height: 26px;
  }
}
.catalog {
  width: 100%;
}

.products {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1440px) {
  .products {
    justify-content: space-around;
  }
}
@media (max-width: 425px) {
  .products {
    justify-content: flex-start;
    padding: 0 40px 0 20px;
  }
}

.products-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(4, 334px);
  gap: 20px;
  order: 2;
}
@media (max-width: 1440px) {
  .products-list {
    grid-template-columns: repeat(3, 334px);
  }
}
@media (max-width: 1024px) {
  .products-list {
    grid-template-columns: 334px 334px;
  }
}
@media (max-width: 768px) {
  .products-list {
    grid-template-columns: 260px 260px;
  }
}
@media (max-width: 425px) {
  .products-list {
    display: flex;
    max-width: 100%;
    width: 100%;
    overflow: hidden;
  }
}

.products-card {
  border: 1px solid #ecf0f4;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 425px) {
  .products-card {
    min-width: 260px;
  }
}

.products-card-image-wrapper {
  position: relative;
}
.products-card-icon-link {
  text-decoration: none;
  position: absolute;
  right: 20px;
  width: 34px;
  height: 34px;
  background-color: #ffffff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in;
  outline: none;
}
.products-card-icon-link:hover, .products-card-icon-link:focus {
  transform: scale(1.3);
}
.products-card-icon-link-like-link {
  top: 20px;
}
.products-card-icon-link-substract-link {
  top: 59px;
}
.products-card-icon-link-hit-link, .products-card-icon-link-new-link {
  width: 50px;
  height: 50px;
  background-color: none;
  border-radius: none;
  top: 20px;
  left: 20px;
}

.products-card-image {
  display: block;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
}
@media (max-width: 768px) {
  .products-card-image {
    object-fit: contain;
    object-position: center;
    max-height: 260px;
  }
}

.product-card-pagination {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  max-width: 120px;
}

.product-card-pagination-item {
  margin-right: 4px;
}

.product-card-pagination-button {
  padding: 0;
  width: 20px;
  height: 2px;
  margin: 0;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #cccccc;
}
.product-card-pagination-button.active {
  background-color: #f1d264;
}

.products-card-content {
  text-align: center;
  padding: 10px 25px 25px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
@media (max-width: 768px) {
  .products-card-content {
    padding: 10px 20px 20px;
  }
}

.products-card-title-link {
  text-decoration: none;
  display: block;
  margin-bottom: 5px;
  flex-grow: 1;
  outline: none;
}
.products-card-title-link:focus h3, .products-card-title-link:hover h3 {
  color: #dbaf14;
}
.products-card-title-link:active h3 {
  color: #edc435;
}

.products-card-title {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  transition: all 0.3s ease-in;
}
@media (max-width: 768px) {
  .products-card-title {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 15px;
  }
}

.products-card-price-wrapper {
  margin-bottom: 10px;
}

.products-card-price {
  font-size: 20px;
  line-height: 22px;
  color: #000000;
  margin-right: 10px;
}

.products-card-price_old {
  font-size: 14px;
  line-height: 22px;
  color: #8D9295;
  text-decoration: line-through;
}

.products-card-button-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: auto;
}
@media (max-width: 768px) {
  .products-card-button-wrapper {
    justify-content: center;
  }
}

.products-card-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 13px;
  background-color: #f1d264;
  padding: 0 20px;
  min-width: 174px;
  text-decoration: none;
  color: #000000;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  border-radius: 5px;
  transition: all 0.3s ease-in;
  outline: none;
}
.products-card-button:hover, .products-card-button:focus {
  background-color: #dbaf14;
}
.products-card-button:active {
  background-color: #edc435;
}
@media (max-width: 768px) {
  .products-card-button {
    padding: 16px 20px;
  }
}

.control-button {
  width: 40px;
  height: 40px;
  padding: 0;
  margin: 0;
  border: 1px solid #ecf0f4;
  border-radius: 5px;
  position: relative;
  background-color: transparent;
  transition: all 0.3s ease-in;
}
.control-button:hover, .control-button:focus {
  border-color: #f1d264;
}
@media (max-width: 425px) {
  .control-button {
    display: none;
  }
}

.control-button svg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.button-prev {
  order: 1;
  transform: rotate(-180deg);
}

.button-next {
  order: 3;
}

.counter {
  background-color: #ffffff;
  border-radius: 5px;
  border: 1px solid #ecf0f4;
  width: 100px;
  max-width: 100px;
  display: flex;
  align-items: center;
  justify-items: center;
  text-align: center;
  padding: 10px 5px;
}
@media (max-width: 768px) {
  .counter {
    margin-bottom: 10px;
  }
}

.counter-button {
  padding: 0;
  background-color: transparent;
  border: none;
  width: 100%;
  height: 30px;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease-in;
  border-radius: 5px;
}
.counter-button:hover {
  box-shadow: inset 0 0 2px 0 #abbdcf;
}

.counter-button-icon {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.counter-input {
  width: 30px;
  padding: 0 5px;
  font-family: inherit;
  font-size: 18px;
  font-weight: normal;
  line-height: 21px;
  text-align: center;
  border-radius: 0;
  color: #4c4e50;
  border: none;
  outline: none;
  -webkit-appearance: textfield;
  appearance: textfield;
  background-color: transparent;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}